import React from "react";
import { useRef } from "react";
import { useTranslation } from "react-i18next";
import { useDataProvider } from "../../provider/DataProvider";
import style from "./update_product_price.module.css";
const UpdateProductPrice = () => {
  const {t} = useTranslation()
  const { updateProductPrice } = useDataProvider();
  const productNameInp = useRef();
  const productPriceInp = useRef();
  return (
    <div className={style.container}>
      <div className={style.container2}>
        <input className={style.inp} type="text" placeholder="product name" ref={productNameInp} />
        <input className={style.inp} type="text" placeholder="price" ref={productPriceInp} />
        <button className={style.change_btn}
          onClick={() => {
            const productName = productNameInp.current.value;
            const productPrice = productPriceInp.current.value;
            updateProductPrice(productName, productPrice);
          }}
        >
          {t("update")}
        </button>
      </div>
    </div>
  );
};

export default UpdateProductPrice;
