import React, { useRef } from "react";
import { useTranslation } from "react-i18next";
import { useDataProvider } from "../../provider/DataProvider";
import style from "./add-product-page.module.css";
const AddProductsPage = () => {
  const {t} = useTranslation()
  const nameInp = useRef();
  const priceInp = useRef();
  const categoryInp = useRef();
  const imgInp = useRef();
  const { addProducts } = useDataProvider();

  const inp = [
    { type: "text", placeholder: "name", ref: nameInp },
    { type: "number", placeholder: "price", ref: priceInp },
    { type: "text", placeholder: "category", ref: categoryInp },
    { type: "file", placeholder: "category", ref: imgInp },
  ];
  const checkInp = (name, price) => {
    let incorrectDetails = false;
    if (name.length < 3) incorrectDetails = true;
    if (price < 2) incorrectDetails = true;
    return incorrectDetails;
  };
  const handleClick = (e) => {
    e.preventDefault();
    const addedProducts = {
      name: nameInp.current.value,
      price: priceInp.current.value,
      category: categoryInp.current.value,
      picture: imgInp.current.value,
    };
    if (checkInp(addedProducts.name, addedProducts.price)) return alert("incorrect details");
    addProducts(addedProducts);
  };
  return (
    <form className={style.form_container} onSubmit={handleClick}>
      <div>
        <h1 id={style.add_product_tag}>add product!</h1>
        <div className={style.add_product_container}>
          {inp.map((input, index) => {
            return (
              <input
                key={index}
                className={style.inp}
                type={input.type}
                placeholder={input.placeholder}
                ref={input.ref}
              ></input>
            );
          })}

          <button id={style.add_btn}>{t('add')}</button>
        </div>
      </div>
    </form>
  );
};

export default AddProductsPage;
