import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import ProductsPage from "../productsPage/ProductsPage";
import DeleteProductsPage from "../deleteProductsPage/DeleteProductsPage";
import AddProductsPage from "../addProductsPage/AddProductsPage";
import UsersPage from "../usersPage/UsersPage";
import UpdateProductPrice from "../update_product_price/UpdateProductPrice";
import style from "./admin-page.module.css";
import { useDataProvider } from "../../provider/DataProvider";
import { useTranslation } from "react-i18next";
const AdminPage = () => {
  const { t } = useTranslation();
  const [toggleAdminOptions, setToggleAdminOption] = useState(0);
  const navigate = useNavigate();
  const { users } = useDataProvider();
  const handleClick = (num) => {
    setToggleAdminOption(num);
  };
  const buttons = [
    { label: "products", num: 1 },
    { label: "delete products", num: 2 },
    { label: "add product", num: 3 },
    { label: "update price", num: 4 },
    { label: "users", num: 5 },
    { label: "logout", num: 6 },
  ];
  return (
    <>
      <div className={style.admin_container}>
        {buttons.map((buttons) => {
          return (
            <button
              key={buttons.num}
              className={style.buttons}
              onClick={() => {
                if (buttons.label === "logout") return navigate("/");
                handleClick(buttons.num);
              }}
            >
              {t(buttons.label.replace(/\s/g, ""))}
            </button>
          );
        })}
      </div>
      {toggleAdminOptions === 1 && <ProductsPage />}
      {toggleAdminOptions === 2 && <DeleteProductsPage />}
      {toggleAdminOptions === 3 && <AddProductsPage />}
      {toggleAdminOptions === 4 && <UpdateProductPrice />}
      {toggleAdminOptions === 5 &&
        users.map((user) => {
          return <UsersPage key={user.id} {...user} />;
        })}
    </>
  );
};

export default AdminPage;
