import React from "react";
import style from "./user_page.module.css";
import ShoppingHistory from "../shopping_history/ShoppingHistory";
const UsersPage = ({ id, name, shoppingHistory }) => {
  return (
    <div className={style.container}>
      <div className={style.user_container}>
        <h1 className={style.user_details}> name : {name}</h1>
        <h1 className={style.user_details}> id : {id}</h1>
        {shoppingHistory.map((product,index) => {
 return <ShoppingHistory key={index} product  = {product}/>
    })
     
         
        }
      </div>
    </div>
  );
};

export default UsersPage;
