import React from "react";
import { useTranslation } from "react-i18next";
import { useDataProvider } from "../../provider/DataProvider";
import "./delete-products-page.css";
const DeleteProductsPage = () => {
  const { t } = useTranslation();
  const { deleteProduct, products } = useDataProvider();
  return (
    <div className="delete-container">
      <div className="delete-products-container">
        <h1 id="delete-product-tag">delete products!</h1>
        {products.map((product, index) => {
          return (
            <div className="product-delete-tag" key={index}>
              {product.name}
              <button
                className="delete-buttons"
                onClick={() => {
                  deleteProduct(product.name);
                }}
              >
                {t("delete")}
              </button>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default DeleteProductsPage;
