import { useContext, createContext, useState } from "react";
import productsArr from "./data";
import "../components/LanguageData";

export const DataContext = createContext();
export const useDataProvider = () => {
  return useContext(DataContext);
};

const DataProvider = ({ children }) => {
  const [products, setProducts] = useState(productsArr);
  const [users, setUsers] = useState([]);
  const [cart, setCart] = useState([]);
  const [toggleLogOut, setToggleLogOut] = useState(false);
  const [toggleSort, setSort] = useState(false);
  const addProducts = (addedProducts) => {
    setProducts((prev) => {
      return [...prev, addedProducts];
    });
  };
  const deleteProduct = (productName) => {
    const afterDeleteFilter = products.filter((product) => {
      return product.name !== productName;
    });
    setProducts(afterDeleteFilter);
  };
  const addUser = (name, id, password, confirmPassword) => {
    const user = {
      name,
      id,
      password,
      confirmPassword,
      shoppingHistory: [],
    };
    setUsers((prev) => {
      return [...prev, user];
    });
  };
  const specificUser = (password, name) => {
    const findUser = users.find((user) => {
      return user.password === password && user.name === name;
    });
    if (findUser) setToggleLogOut(true);
    return findUser;
  };

  const addProductToUser = (id, cart) => {
    const afterMap = users.map((user) => {
      if (user.id !== id) return {...user}
      let temp = [...user.shoppingHistory ,{shoppingHistory : cart} ]
 
      return {...user , shoppingHistory : temp}

    });
    setUsers(afterMap);
    setCart([]);
  };
  const isUserExist = (id) => {
    const checkUser = users.find((user) => {
      return user.id === id;
    });
    return checkUser;
  };
  const addToCart = (productName, price) => {
    setCart((prev) => {
      return [...prev, { productName: productName, price: price }];
    });
  };
  const updateProductPrice = (productName, price) => {
    const afterMap = products.map((product) => {
      return product.name === productName ? { ...product, price: Number(price) } : { ...product };
    });
    setProducts(afterMap);
  };
  const sortProductsByPrice = (sortPrice) => {
    products.sort((a, b) => {
      if (sortPrice === "high") {
        if (a.price < b.price) return 1;
        if (a.price > b.price) return -1;
        return 0;
      }
      if (sortPrice === "low") {
        if (a.price < b.price) return -1;
        if (a.price > b.price) return 1;
        return 0;
      }
    });

    setSort((prev) => {
      return !prev;
    });
  };
  return (
    <DataContext.Provider
      value={{
        toggleLogOut,
        setToggleLogOut,
        sortProductsByPrice,
        updateProductPrice,
        isUserExist,
        deleteProduct,
        addProducts,
        products,
        addUser,
        users,
        specificUser,
        addProductToUser,
        addToCart,
        cart,
      }}
    >
      {children}
    </DataContext.Provider>
  );
};
export default DataProvider;
